<template>
    <div class="mui-content">
        <v-slider :sliderArr = "sliderArr"></v-slider>
        <nav class="nav">
            <ul class="nav-list">
                <li class="nav-item"><a><span class=""></span></a></li>
                <li class="nav-item"><a><span></span></a></li>
                <li class="nav-item"><a><span></span></a></li>
                <li class="nav-item"><a><span></span></a></li>
                <li class="nav-item"><a><span></span></a></li>   
            </ul>
        </nav>
    </div>
</template>

<script>
const COMMONURL = "http://192.168.1.91:8088/framework/pcrest";
const SUCCESS_CODE = 5600;
import vSlider from '@/components/mobile/slider';
export default {
    data:function(){
        return {
            sliderArr:[]
        }
    },
    components:{
        vSlider
    },
    methods:{
        getData:function(){
            let _this = this;
            $.ajax({
                url:COMMONURL+"/index",
                data:{'platForm':'020003'},
                asnyc:true,
                dataType:"json",
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        let indexData = data.data; 
                        console.log(indexData);                   
                        _this.sliderArr = indexData.carousel;
                    }
                }
            })
        }
    },
    mounted:function(){
        this.getData();
    }
}
</script>



