<template>
    <div id="mui-slider" class="mui-slider">
        <div class="mui-slider-group mui-slider-loop" v-if = "getNewSliderArr.length" >
            <div class="mui-slider-item" v-for="(item,index) in getNewSliderArr" :key="index" :class="(index == 0)||(index == getNewSliderArr.length-1) ? 'mui-slider-item-duplicate':''">
                <a href="">
                    <img :src="item.imgUrl" alt="">
                </a>
            </div>
        </div>
        <div class="mui-slider-indicator" v-if = "sliderArr.length">
            <div class="mui-indicator" v-for="(item,index) in sliderArr" :key="index" :class="(index == 0)? 'mui-active':''"></div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        sliderArr:Array
    },
    computed:{
        getNewSliderArr(){
            let newSliderArr;
            if(this.sliderArr && this.sliderArr.length != 0){
                let firstItem = this.sliderArr[this.sliderArr.length-1];
                let lastItem = this.sliderArr[0];
                newSliderArr = Array.from(this.sliderArr);
                newSliderArr.unshift(firstItem);
                newSliderArr.push(lastItem);
                return newSliderArr;
            }else{
                return newSliderArr = [];
            }           
        }
    },
    methods:{
        initSlider:function(){
            setTimeout(function(){
                mui.init();
                mui('#mui-slider').slider({interval: 5000});
            },500)    
        }
    },
    mounted:function(){
        this.initSlider();
    }  
}
</script>



