import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/pc/home'
import mobileIndex from '@/pages/mobile/home'

Vue.use(Router)

let pcRouter = new Router({
    routes: [
      {
        path: '/',
        component:index,
        name:"index"
      }
    ]
})

let mobileRouter = new Router({
    routes: [
      {
        path: '/',
        component:mobileIndex,
        name:"index"
      }
    ]
})

let deviceWidth = document.body.clientWidth;

let router;

if(deviceWidth < 1200){
  router = mobileRouter;
}else{
  router = pcRouter;
}

export default router;


